.banner{
 background-size: cover;
    
    height: 543px;
    color: white;
}
.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 20px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    outline: none;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);cursor: pointer;
    margin-right: 1rem;
    

}
.button:hover{
    color: black;
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    height: 80px;
    max-width: 360px;
   
}
.fade{
    height: 13.5rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}